@import url(https://fonts.googleapis.com/css2?family=Rampart+One&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: sans-serif;
}

.btn {
  background-color: #00a651;
  color: white;
  border: none;
  text-decoration: none;
  cursor: pointer;
  padding: 10px 25px;
  text-align: center;
}

.btn:hover{
  background-color: #028642;
}

.message {
  padding: 10px;
  margin-bottom: 1rem;
  background-color: rgb(234, 234, 234);
  color: red;
}

.header {
	 background-color: rgb(255, 255, 255);
	 height: 5rem;
	 display: flex;
	 justify-content: space-between;
	 align-items: center;
	 padding: 0px 20px;
	 box-shadow: 0px 5px 5px 5px #ddd;
}
 .header_logo {
	 color: black;
	 text-decoration: none;
	 font-size: 25px;
	 font-weight: bold;
}

.color{
	color:#00a651;
}
 .header_logoimg img {
	 height: 3.5rem;
}
 .header_search {
	 height: 2rem;
	 background-color: white;
	 width: 35vw;
	 height: 2.5rem;
	 display: flex;
	 align-items: center;
	 border: 1px solid black;
	
}
 .header_search input {
	 flex-grow: 1;
	 font-size: 1.3rem;
	 border: none;
	 outline: none;
	 padding: 10px;
	 border-radius: 25px;
}
 .header_search input[type="text"] {
	 font-size: 1rem;
}
 .header_search-icon {
	 height: 100%;
	 box-sizing: box;
	 padding-right: 15px;
	 display: flex;
	 align-items: center;
	 color: #00a651;
}
 .header_navigation {
	 list-style: none;
	 display: flex;
	 align-items: center;
	 justify-content: center;
	 
}
 .header_navigation_item {
	 margin-right: 1.2rem;
}

.item1{
	padding-top: 10px;
}
.item2{
	padding-top: 6px;
}
 .header_navigation_item-link {
	 font-size: 1.3rem;
	 text-decoration: none;
	 color: rgb(0, 0, 0);
}

.header_navigation_item-link:hover{
	color:#00a651
}

.username{
	font-size: 1.3rem;
	text-decoration: none;
	color: rgb(0, 0, 0);
	cursor:default;
}

 @media only screen and (max-width: 680px) {
	 .header {
		 font-size: 20%;
	}
	 .header_search {
		 display: none;
	}
	 .header_logo {
		 font-size: 22px;
	}
	 .header_navigation_item-link, .username{
		 font-size: 1.2rem;
	}
}
 @media only screen and (max-width: 400px) {
	 .header {
		 padding: 1rem;
	}
	 .header_navigation_item {
		 margin-right: 1rem;
	}
	.header_logo {
		font-size: 20px;
   } 
   .btn{
	padding: 8px 12px;
   }
}
 
.home {
  display: flex;
  flex-direction: column;
  background-color: #ededed;
  min-height: 100vh;
  width: 100%;
  padding-bottom: 2.4rem;
}
.home_carousel {
  width: 100%;
  height: 24rem;
}
.heading{
  font-size:28px;
  padding: 45px 0;
  text-align: center;
}
.home_products {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}
.card {
  height: 27rem;
  width: 17rem;
  display: flex;
  flex-direction: column;
  color: #555;
  margin: 0 1rem 2.4rem 1rem;
  background-color: #fff;
  border-radius: 5px;
}
.card-img {
  height: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.card-img img {
  width: 14rem;
  padding: 0;
}
.card-details {
  height: 50%;
  padding: 0.5rem 1.5rem;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
.card-details-rating {
  display: flex;
}
.card-details-rating p {
  margin-left: 1rem;
}


.carousel {
  position: relative;
  font-size: 1rem;
  color: black;
}
.carousel_slide {
  padding: 1rem;
  background-image: linear-gradient(15deg, #13547a 0%, #80d0c7 100%);
  display: flex;
  justify-content:center;
  flex-direction: row;
  align-items: center;
  height: 24rem;
  color: white;
  position: absolute;
  width: 100%;
}
.carousel_slide-title {
  font-size: 1.5rem;
  color: white;
  text-decoration: none;
}
.carousel_slide img {
  border-radius: 50%;
  width: 200px;
  height: 200px;
  padding: 0;
}
.carousel_slide-right {
  position: absolute;
  right: 2rem;
  top: 45%;
  font-size: 2rem;
  cursor: pointer;
}
.carousel_slide-left {
  position: absolute;
  left: 2rem;
  top: 45%;
  font-size: 2rem;
  cursor: pointer;
}
.carousel_slide-visible {
  z-index: 1111;
}

@media only screen and (max-width: 1000px) {
  .carousel_slide img {
    width: 180px;
    height: 180px;
  }
}
@media only screen and (max-width: 780px) {
  .carousel_slide img {
    width: 160px;
    height: 160px;
  }
  .carousel_slide-title {
    font-size: 1.3rem;
  }
  .carousel {
    font-size: 0.8rem;
  }
}

@media only screen and (max-width: 620px) {
  .carousel_slide{
     flex-direction: column-reverse;
     text-align: center;
     justify-content: flex-end;
     padding-top: 50px;
  }
}
@media only screen and (max-width: 415px) {
  .carousel_slide img{
    width: 140px;
    height: 140px;
  }
  .carousel_slide-title {
    font-size: 1.2rem;
  }
  .carousel {
    font-size: 0.6rem;
  }
}

.footer {
  background-color: #0c4271;
  color: white;
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 5rem;
}
.footer ul {
  display: flex;
  list-style: none;
}
.footer li {
  margin-right: 2rem;
  cursor: pointer;
}
@media only screen and (max-width: 500px) {
  .footer {
    flex-direction: column;
    padding: 1rem;
    height: 7rem;
 }
}
@media only screen and (max-width: 320px) {
  .footer {
    padding: 1rem;
    height: 7rem;
    font-size: 14px;
 }
  .footer li {
    margin-right: 1.2rem;
 }
}

.footer-1 {
  background-color: #232f3e;
  color: white;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  height: 5rem;
  font-size: 18px;
}

.icons{
  font-size: 24px;
}

.icon:hover{
  color: #00a651;
}

.footer-1 ul{
  list-style-type: none;
  display: flex;
}

.footer-1 li{
  margin: 2vw;
  cursor: pointer;
}

.footer-1 li:hover{
  color: #00a651;
}

.footer-2 {
  display: flex;
  background-color: #131a22;
  color: white;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  height: 3.5rem;
  font-size: 16px;
}

@media only screen and (max-width: 680px) {
  .footer-1 {
    flex-direction: column;
    height: 7rem;
 }
}
@media only screen and (max-width: 380px) {
  .footer-1 {
    padding: 1rem;
    height: 7rem;
    font-size: 14px;
 }

 .footer-2{
  font-size: 14px;
 }
 .icons{
  font-size: 20px;
}
}

.login {
  min-height: 80vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.login_box {
  width: 30rem;
  min-height: 25rem;
  box-shadow: 0 5px 5px 5px #ddd;
  padding: 2.5rem;
  margin: 1.5rem;
}
.login_box-title {
  font-size: 1.8rem;
  margin-bottom: 2rem;
  text-transform: uppercase;
  font-weight: 600;
  color: black;
}
.login_box-input {
  display: flex;
  flex-direction: column;
}
.login_box-input label {
  margin-bottom: 0.6rem;
  font-size: 1.2rem;
}
.login_box-input input {
  width: 20rem;
  margin-bottom: 2rem;
  font-size: 1.2rem;
  padding: 10px;
  outline: none;
  background-color: #eee;
  border: none;
}
.login_box-input input[type="text"] {
  font-size: 1rem;
}
.login_box-input input::-webkit-input-placeholder {
  font-size: 0.9rem;
}
.login_box-input input:-ms-input-placeholder {
  font-size: 0.9rem;
}
.login_box-input input::placeholder {
  font-size: 0.9rem;
}
.login_box-register {
  margin-top: 1rem;
}
.login_box-register-link {
  color: #00a651;
  text-decoration: none;
}
@media only screen and (max-width: 500px) {
  .login_box {
    box-shadow: none;
 }
}
@media only screen and (max-width: 320px) {
  .login_box-input {
    font-size: 80%;
 }
  .login_box-input input {
    width: 15rem;
 }
}

.details {
  margin: 30px 20px 60px 20px;
  min-height: 80vh;
}

.details_image {
  margin-top: 30px;
  width: 34rem;
}
.details_image img {
  height: 24rem;
}
.details_main {
  display: flex;
  flex-direction: row;
}
.details_content {
  display: flex;
  padding-top: 2rem;
}
.details_content-info {
  color: #555;
  padding-right: 2rem;
  width: 40vw;
}
.details_content-info-added {
  background-color: rgb(234, 234, 234);
  color: #333;
  padding: 10px;
  margin-top: 10px;
}
.details_content-info h1 {
  margin-bottom: 1rem;
}
.details_content-info-rating {
  display: flex;
  margin: 1rem 0;
}
.details_content-info-price {
  margin: 1rem 0;
}
.details_content-info-description {
  margin: 1rem 0;
}
.details_content-price {
  width: 200px;
  border: 1px solid black;
  height: 18rem;
  border-bottom: none;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}
table{
  height: 230px;
  font-size: 18px;
  border: thin solid rgb(205, 205, 205);
  border-collapse: collapse;
  margin-top: 5px;
}
td{
  padding: 10px 15px 20px 15px;
  
}
tr{
  outline: thin solid rgb(205, 205, 205);
}
.details_content-price div {
  padding: 1rem;
  border-bottom: 1px solid black;
}
.details_content-price label {
  margin-right: 5px;
}
.details_content-price input {
  font-size: 1rem;
  outline: none;
}
input{
  width: 70px;
  height: 30px;
}

@media only screen and (max-width: 1200px){
  img{
    width: 26rem;
  }
  .details_image img {
    height: 20rem;
  }
  .details_content-info{
    width: 38vw;
   }
}
@media only screen and (max-width: 1088px) {
  img {
    width: 22rem;
 }
 .details_image img {
  height: 18rem;
}
