}
.register_box-input input::-webkit-input-placeholder {
  font-size: 0.9rem;
}
.register_box-input input:-ms-input-placeholder {
  font-size: 0.9rem;
}
.register_box-input input::placeholder {
  font-size: 0.9rem;
}
.register_box-login {
  margin-top: 1rem;
}
.register_box-login-link{
  color: #00a651;
  text-decoration: none;
}
@media only screen and (max-width: 414px) {
  .register_box-input input, .register_box-input textarea {
